<template>
  <v-app>
    <v-app-bar app>
      <v-app-bar-nav-icon @click.stop="mini = !mini">
        <v-icon v-if="mini">mdi-format-indent-increase</v-icon>
        <v-icon v-else>mdi-format-indent-decrease</v-icon>
      </v-app-bar-nav-icon>

      <v-divider class="mx-3" inset vertical></v-divider>

      <v-toolbar-title>{{ $route.name }}</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-divider class="mx-3" inset vertical></v-divider>

      <v-badge
        class="mr-5"
        :color="stock > 0 ? 'error' : ''"
        :content="stock > 0 ? stock : ''"
        overlap
      >
        <v-icon>mdi-bell</v-icon>
      </v-badge>

      <v-divider class="mx-3" inset vertical></v-divider>

      <v-btn icon @click="setting_Drawer = true">
        <v-icon>mdi-cog-outline</v-icon>
      </v-btn>

      <v-divider class="mx-3" inset vertical></v-divider>

      <v-menu
        v-model="menu_bar"
        offset-y
        origin="top right"
        transition="scale-transition"
      >
        <template v-slot:activator="{ on }">
          <v-btn icon v-on="on">
            <v-icon>mdi-account-settings-outline</v-icon>
          </v-btn>
        </template>

        <v-card>
          <v-list>
            <v-list-item>
              <v-list-item-icon>
                <v-icon>mdi-key-outline</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Change Password</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-divider></v-divider>
            <v-list-item to="Profile">
              <v-list-item-icon>
                <v-icon>mdi-account-cog-outline</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Profile</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-divider></v-divider>
            <v-list-item @click="dialog_logout = true">
              <v-list-item-icon>
                <v-icon>mdi-power</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>LOGOUT</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-card>
      </v-menu>
    </v-app-bar>

    <v-navigation-drawer v-model="setting_Drawer" right temporary fixed>
      <v-row class="px-5 py-2">
        <v-spacer></v-spacer>
        <v-btn icon @click="setting_Drawer = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-row>

      <v-divider></v-divider>

      <v-list nav>
        <v-list-item class="my-n5">
          <v-list-item-content>
            <v-list-item-title>Dark Mode</v-list-item-title>
          </v-list-item-content>
          <v-list-item-icon>
            <v-switch v-model="Dark" inset :label="Dark ? 'ON' : 'OFF'" />
          </v-list-item-icon>
        </v-list-item>

        <v-divider></v-divider>

        <v-list-item>
          <v-list-item-content>
            <v-list-item-title>Language</v-list-item-title>
          </v-list-item-content>
          <v-list-item-icon class="my-0">
            <Language width="80px" elevation="2" />
          </v-list-item-icon>
        </v-list-item>

        <v-divider />
      </v-list>
    </v-navigation-drawer>

    <v-navigation-drawer
      v-model="sidebar_Drawer"
      app
      permanent
      :mini-variant="mini"
      mini-variant-width="80"
      :class="$vuetify.theme.dark ? 'grey darken-4' : 'grey lighten-4'"
    >
      <v-list-item :class="mini ? 'px-2' : ''">
        <v-list-item-avatar>
          <v-img src="@/assets/images/LOGO.png" />
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title class="title">
            {{ user.username }}
          </v-list-item-title>
          <v-list-item-subtitle>
            {{ user.u_email }}
          </v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>

      <v-divider class="mt-2" />

      <v-list :dense="mini" nav>
        <MenuDrawer
          v-for="rout in routlist"
          :key="rout.route"
          :route="'/'+ $i18n.locale + rout.route"
          :mini="mini ? '' : 'pl-2'"
          :icon="rout.icon"
          :title="rout.text"
          :divider="mini ? 'mb-1' : 'mb-2'"
        />
      </v-list>
    </v-navigation-drawer>

    <v-main :class="$vuetify.theme.dark ? '' : 'grey lighten-2'">
      <v-container>
        <v-fade-transition mode="out-in">
          <router-view></router-view>
        </v-fade-transition>
      </v-container>
    </v-main>

    <v-dialog v-model="dialog_logout" max-width="320">
      <v-card class="pa-3">
        <v-card-title class="headline">LOGOUT</v-card-title>
        <v-card-text>You want to LOGOUT NOW!</v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn dark color="green darken-1" @click="LOGOUT">Yes</v-btn>
          <v-btn dark color="error" @click="dialog_logout = false">NO</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import Vue from 'vue'
import MenuDrawer from '@/components/MenuDrawer'
import Language from '@/components/Language'
import axios from '@/plugins/axios'
export default {
  name: 'Admin',
  components: { MenuDrawer, Language },
  data: () => ({
    mini: true,
    setting_Drawer: false,
    menu_bar: false,
    sidebar_Drawer: true,
    dialog_logout: false,
    Dark: Vue.$cookies.get('Dark') === 'true' ? 1 : 0,
    stock: '',
    user: {},
    routlist: [
      {
        text: 'Dashboard',
        route: '/dashboard',
        icon: 'view-dashboard'
      },
      {
        text: 'Users',
        route: '/users',
        icon: 'account-tie'
      },
      {
        text: 'Stores',
        route: '/stores',
        icon: 'store'
      },
      {
        text: 'Customers',
        route: '/customers',
        icon: 'account-group'
      },
      {
        text: 'Listsale',
        route: '/listsale',
        icon: 'clipboard-text-outline'
      },
      {
        text: 'Sales',
        route: '/sales',
        icon: 'cash-register'
      }
    ]
  }),
  mounted () {
    // this.Notifications()
    // this.user = jwt.decode(this.$cookies.get('loggedIn'))
  },
  watch: {
    Dark (val) {
      this.$cookies.set('Dark', val)
      const setdark = this.$cookies.get('Dark') === 'true' ? 1 : 0
      this.$vuetify.theme.dark = setdark
    }
  },
  methods: {
    LOGOUT () {
      this.$store.commit('SET_LOGIN', '')
      this.$cookies.remove('loggedIn')
      this.$router.go({ name: 'Login' })
    },
    async Notifications () {
      await axios.post('Home/notifications')
        .then((res) => {
          if (res.data.success) {
            const data = res.data.stock
            data.forEach(element => {
              this.stock += element.stock
            })
          }
        })
    }
  }
}
</script>
<style>
.fade-enter-active,
.fade-leave-active {
  transition-duration: 0.3s;
  transition-property: opacity;
  transition-timing-function: ease;
}

.fade-enter,
.fade-leave-active {
  opacity: 0
}
</style>
